import React, { Component, PureComponent } from 'react';

import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip, BarChart, Legend, Bar } from 'recharts';

import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';

import { lodashToggle, jsDateTimeToStr, tsToJsDatetime, jsDateTimeToTs, tsToStr, strDateToTs } from '../utils';


export function genData (data, param) {
  let dataParams = []
  data.map(record => {
    if (record.record && record.record[param] )
      dataParams = lodashToggle(dataParams, {ts: tsToStr(record.ts), [param]: record.record[param]})
  })
  return dataParams
}

function getModalStyle() {
  const top = 50 
  const left = 50

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: 'absolute',
    // width: 1000,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function ChartBody(props) {
  const { t1, t2, setDate, chartType, showChart } = props
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);

  return (
    <div style={modalStyle} className={classes.paper}>
      <div className='d-flex justyfy-content-between mb-3'>
        <h4 className='mr-5'>{props.label}</h4>
        <div className=''>
          <TextField
            type="date"
            className='mr-2'
            InputLabelProps={{
              className: '',
              shrink: true 
            }}
            name='t1'
            value={t1}
            onChange={setDate}
          />
          <TextField
            type="date"
            InputLabelProps={{
              className: '',
              shrink: true 
            }}
            name='t2'
            value={t2}
            onChange={setDate}
          />
          <button className='btn btn-sm bg-blue2-light ml-1' onClick={showChart}>
            Показать
          </button>
        </div>
      </div>
      {chartType === 'bar'
        ? <RenderBarChart data={props.data} param={props.param} />
        : <RenderLineChart data={props.data} param={props.param}/>
      }
      
    </div>
  );
}

export class ChartModal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      t1: null,
      t2: null,
    }
    this.setDate = this.setDate.bind(this);
    this.showChart = this.showChart.bind(this);
  }

  componentDidMount() {
      let t2 = new Date();
      let t1 = new Date().setDate(t2.getDate() - 1);

      if (this.props.query.period === 'weekly') {
        t1 = new Date().setDate(t2.getDate() - 7);
      }
      
      let query = {ws: this.props.query.ws, sect: '', t1: t1, t2: jsDateTimeToTs(t2),
         param: this.props.query.param,}
      if (this.props.query.sect)
        query['sect'] = this.props.query.sect
      this.props.getESystemsDataDetail(query)

      this.setState({
        t1: tsToStr(t1).split('T')[0],
        t2: jsDateTimeToStr(t2).split('T')[0]
      })
  }

  setDate (e) {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  showChart () {
    let query = {ws: this.props.query.ws, sect: '', 
        t1: strDateToTs(this.state.t1), 
        t2: strDateToTs(this.state.t2),
        param: this.props.query.param,}
    if (this.props.query.sect)
      query['sect'] = this.props.query.sect
    this.props.getESystemsDataDetail(query)
  }

  render () {
    const { detailDataList } = this.props
    const data = genData(detailDataList, this.props.query.param)
    const chartType = this.props.query.period === 'weekly' ? 'bar' : 'line'
    
    return (
      <ChartBody data={data} label={this.props.label} param={this.props.query.param}
        setDate={this.setDate} t1={this.state.t1} t2={this.state.t2} chartType={chartType}
        showChart={this.showChart}
        />
    )
  }
}

class CustomizedAxisTick extends PureComponent {
  render() {
    const { x, y, stroke, payload } = this.props;
    let date 
    let time

    if (payload.value) {
      date = payload.value.split('T')[0]
      time = payload.value.split('T')[1]
    }

    return (
      <g transform={`translate(${x},${y})`}>
        <text x={0} y={0} dy={16} className='font-10'>
          {date}
        </text>
        <text x={0} y={0} dy={7}>
          {time}
        </text>
      </g>
    );
  }
}


export function RenderLineChart (props) {
  const { data, param } = props
  return (
    <LineChart width={700} height={200} data={data}>
      <Line type="monotone" dataKey={param} stroke="#BF263C" />
      <CartesianGrid stroke="#ccc" />
      <XAxis dataKey="ts" 
        // tickCount={5}
        tick={<CustomizedAxisTick />} 
        />
      <YAxis />
      <Tooltip />
    </LineChart>
  )
}

function RenderBarChart (props) {
  const { data, param } = props
  return (
    <BarChart width={700} height={200} data={data}>
      <CartesianGrid strokeDasharray="3 3" />
      {/* <CartesianAxis y={10}/> */}
      <XAxis dataKey="ts" tick={<CustomizedAxisTick />}/>
      <YAxis ticks={[1,2,3,4,5,6,7,8]} />
      <Tooltip />
      <Legend margin={{top: '50'}}/>
      <Bar dataKey={param} fill="#8884d8" />
    </BarChart>
  )
}